.search-block {
  margin: 0 auto;
  width:250px;
  height:50px;
  background: rgb(77,221,249);
  background: #45aaf2;
  overflow:hidden;
  border-radius:100px;
  position:relative;
  box-shadow: 0px 10px 15px -5px rgba(0,0,0,0.4);
}

.search-block .icon{
  position:absolute;
  pointer-events:none;
  left:14px;
  top:50%;
  transform:translateY(-50%);
  transition-duration:.8s;
  z-index:0;
}

.search-block:hover .icon{
  transform:translateY(-50%) scale(1.3) rotate(3deg);
}

.search-block .icon:before{
  margin-top:-4px;
  display:block;
  content:'';
  height:20px;
  width:20px;
  border-radius:20px;
  background:white;
}

.search-block .icon:after{
  position:absolute;
  bottom:-8px;
  right:5px;
  display:block;
  content:'';
  height:15px;
  width:4px;
  border-radius:20px;
  transform:rotate(-45deg);
  transform-origin:center top;
  background:white;
}

.search-block input[type="search"]{
  height:100%;
  width:100%;
  border:0px;
  background:transparent;
  position:relative;
  outline:0;
  -webkit-appearance:none;
  padding:0px 20px 0px 50px;
  font-size:18px;
  color:#2f3542;
  z-index:2;
}

.search-block input[type="search"]:focus + .icon{
  transform:translate(50%,-50%) scale(24) rotate(180deg);
}

.search-block input[type="search"]::placeholder {
  color: white;
  opacity: 1;
}

.search-block input[type="search"]:-ms-input-placeholder { 
  color: white;
}

.search-block input[type="search"]::-ms-input-placeholder {
  color: white;
}

